<template>
  <div>
    <v-container class="admin-noticias__container">
      <v-row>
        <v-col>
          <show-dialog-noticia
            v-bind="{ value }"
            @procesar="procesar($event)"
            @cerrar="cerrar()"
          />
          <base-material-card
            color="info"
            icon="mdi-view-carousel"
            inline
            title="noticias"
            class="px-5 py-3 mb-5"
          >
            <v-row class="admin-noticias_row__nuevo">
              <v-col class="col-12 col-md-6 text-left">
                <v-text-field
                  v-model="search"
                  label="Buscar por Título"
                  filled
                />
              </v-col>
              <v-col class="col-12 col-md-6 text-right">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="info ma-0"
                      v-bind="attrs"
                      @click="abrirCrear()"
                      v-on="on"
                    >
                      Nueva Noticia &nbsp; <v-icon>mdi-post</v-icon>
                    </v-btn>
                  </template>
                  <span>Crea una nueva Noticia</span>
                </v-tooltip>
              </v-col>
            </v-row>
            <v-simple-table class="admin-noticias__table">
              <thead>
                <tr>
                  <th>Codigo</th>
                  <th>Título</th>
                  <th style="text-align:right;">
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr
                  v-for="(item, i) in noticias"
                  :key="i"
                >
                  <td>{{ item.Id }}</td>
                  <td>{{ item.Titulo }}</td>
                  <td class="text-right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="info px-2 ml-1 btn-edit"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="abrirEditar(item.Id)"
                        >
                          <v-icon>mdi-pencil</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="info px-2 ml-1 btn-delete"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="eliminar(item.Id, item.Titulo)"
                        >
                          <v-icon> mdi-trash-can </v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
            <v-pagination
              v-model="pagina"
              :length="Number(totalPaginas)"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              previous-aria-label="ps"
              next-aria-label="sd"
              color="info"
            />
          </base-material-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { mapState, mapMutations } from 'vuex'
  import swalAlertError from '@/mixins'
  export default {
    components: {
      ShowDialogNoticia: () => import('@/components/base/ShowDialogNoticia'),
    },
    mixins: [swalAlertError],
    data () {
      return {
        value: {
          active: false,
          noticia: {
            Id: '',
            Titulo: '',
            Texto: '',
            Imagen: '',
            Fecha: '',
            Usuario: '',
            MetaTitle: '',
            MetaTags: 0,
          },
          metaTagBox: {
            name: '',
            content: '',
          },
          crear: true,
          imagen: null,
          loader: false,
        },
        noticias: '',
        totalNoticias: '',
        totalPaginas: '',
        pagina: 1,
        limite: 12,
        search: '',
      }
    },
    computed: {
      ...mapState(['token']),
    },
    watch: {
      pagina: function () {
        // console.log(this.pagina)
        if (this.search) {
          this.getNoticias(this.pagina, this.search)
        } else {
          this.getNoticias(this.pagina, '')
        }
      },
      search: function () {
        // console.log(this.search)
        this.pagina = 1 // Seteamos la página en 1 por si pagineó antes
        if (this.search) {
          this.getNoticias(this.pagina, this.search)
        } else {
          this.getNoticias(this.pagina, '')
        }
      },
    },
    created () {
      this.getNoticias(this.pagina)
    },
    methods: {
      ...mapMutations(['cerrarSesion']),
      getNoticias (pagina, byTitleParam = '') {
        const limite = this.limite
        const desde = (pagina - 1) * this.limite
        const bytitle = byTitleParam.split(' ').filter(a => a)
        const body = {
          desde,
          limite,
          bytitle,
        }
        Vue.axios
          .post('/api/noticias', body)
          .then(res => {
            this.noticias = res.data.result
            this.totalNoticias = res.data.total
            this.totalPaginas = Math.round(this.totalNoticias / this.limite)
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getNoticia (id) {
        Vue.axios
          .get(`/api/noticias/noticia/${id}`)
          .then(res => {
            this.value.noticia = res.data.noticia[0]
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      abrirEditar (id) {
        this.getNoticia(id)
        this.value.active = true
      },
      abrirCrear () {
        this.value.active = true
      },
      cerrar () {
        this.value.active = false
        this.value.noticia = {
          Id: '',
          Titulo: '',
          Texto: '',
          Imagen: '',
          Fecha: '',
          Usuario: '',
          MetaTitle: '',
          MetaTags: 0,
        }

        this.value.imagen = null
        this.value.loader = false
        this.value.metaTagBox.name = ''
        this.value.metaTagBox.content = ''
        this.value.crear = true
      },
      procesar (val) {
        // console.log('processing...')
        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        const body = new FormData()
        body.append('Id', val.noticia.Id)
        body.append('Titulo', val.noticia.Titulo)
        body.append('Texto', val.noticia.Texto)
        body.append('Imagen', val.imagen)
        body.append('Usuario', val.noticia.Usuario)
        body.append('MetaTitle', val.noticia.MetaTitle)
        body.append(
          'MetaTags',
          val.noticia.MetaTags !== 0 ? JSON.stringify(val.noticia.MetaTags) : 0,
        ) // Lo pasamos a cadena para después parsearlo en servidor
        body.append('UsuarioCreacion', 1)
        body.append('UsuarioModificacion', 1)

        if (val.noticia.Id) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      create (body, headers) {
        // console.log('create')
        Vue.axios
          .post('/api/noticias/crear', body, { headers })
          .then(res => {
            this.noticias.unshift(res.data.noticia[0])
            this.cerrar()
            Vue.swal('Noticia Creada', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      update (body, headers) {
        // console.log('update')
        Vue.axios
          .put('/api/noticias/editar', body, { headers })
          .then(res => {
            const index = this.noticias.findIndex(
              noticia => noticia.Id === Number(res.data.Id),
            )
            this.noticias[index].Titulo = this.value.noticia.Titulo
            this.cerrar()
            Vue.swal('Noticia Actualizada', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      eliminar (id, titulo) {
        Vue.swal({
          title: `Estás seguro que deseas borrar ${titulo}?`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.axios
              .delete(`/api/noticias/${id}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.noticias.findIndex(
                  n => n.Id === Number(res.data.Id),
                )
                this.noticias.splice(index, 1)
                Vue.swal('Noticia Eliminada', '', 'success')
              })
              .catch(e => {
                this.cerrar()
                this.swalAlertError(e)
              })
          }
        })
      },
    },
  }
</script>
